import React from 'react';
import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { isValid as isValidDate, addDays } from 'date-fns';
import { FieldArray, Form, FormikProvider, getIn, useFormik } from 'formik';
// material
import { Save as SaveIcon } from '@mui/icons-material';
import { DateTimePicker, LoadingButton } from '@mui/lab';
import {
  Box,
  Card,
  Stack,
  TextField,
  CardContent,
  Autocomplete,
  Chip,
  CardHeader,
  InputAdornment,
  FormControlLabel,
  Switch,
  Typography,
  MenuItem,
} from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// utils
import { nameRegExp } from '../../utils/constants/regexs';

// ----------------------------------------------------------------------

const NewBonusSetSchema = Yup.object().shape({
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  brand: Yup.string().required('Brand is required'),
  bonuses: Yup.array(),
  startDate: Yup.date().nullable(true),
  duration: Yup.number().min(1).required('This field is required.'),
  ticketRule: Yup.string(),
  isDefault: Yup.boolean(),
});

BonusSetNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentBonusSet: PropTypes.object,
};

export default function BonusSetNewForm({ isEdit, currentBonusSet }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      name: currentBonusSet?.name || '',
      brand: currentBonusSet?.brand || 'Adidas',
      bonuses: currentBonusSet?.bonuses || [],
      startDate: currentBonusSet?.startDate ? new Date(currentBonusSet.startDate) : null,
      duration: currentBonusSet?.duration || 45,
      ticketRule: currentBonusSet?.ticketRule || undefined,
      isDefault: currentBonusSet?.isDefault || true,
    },
    validationSchema: NewBonusSetSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.bonus.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  console.log('*** ', values, errors);

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Card>
          <CardContent>
            <Stack spacing={3}>
              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                <TextField
                  fullWidth
                  size="small"
                  label="Name"
                  {...getFieldProps('name')}
                  error={Boolean(touched.name && errors.name)}
                  helperText={touched.name && errors.name}
                />
                <TextField
                  select
                  fullWidth
                  size="small"
                  label="Select Brand"
                  placeholder="Brand"
                  {...getFieldProps('brand')}
                  error={Boolean(touched.brand && errors.brand)}
                  helperText={touched.brand && errors.brand}
                >
                  {['Adidas', 'Nike'].map((option, index) => (
                    <MenuItem key={option + index} value={option}>
                      {option}
                    </MenuItem>
                  ))}
                </TextField>
                <DateTimePicker
                  label="Start Date"
                  value={values.startDate ? values.startDate : setFieldValue('startDate', addDays(new Date(), 45))}
                  inputFormat="dd/MM/yyyy hh:mm a"
                  onChange={(date) => (isValidDate(date) ? setFieldValue('startDate', date) : null)}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      size="small"
                      fullWidth
                      error={Boolean(touched.startDate && errors.startDate)}
                      helperText={touched.startDate && errors.startDate}
                      sx={{ mb: 3 }}
                    />
                  )}
                />
                <TextField
                  fullWidth
                  type="number"
                  size="small"
                  label="Duration"
                  InputProps={{
                    endAdornment: <InputAdornment position="end">days</InputAdornment>,
                  }}
                  {...getFieldProps('duration')}
                  error={Boolean(touched.duration && errors.duration)}
                  helperText={touched.duration && errors.duration}
                />
              </Stack>
              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                <Autocomplete
                  fullWidth
                  multiple
                  disableCloseOnSelect
                  size="small"
                  value={values.bonuses}
                  onChange={(event, newValue) => {
                    console.log(newValue);
                    setFieldValue('bonuses', newValue);
                  }}
                  options={[
                    { name: 'Bonus 1', quantity: 100, quantityToAssign: 0, value: 20 },
                    { name: 'Bonus 2', quantity: 50, quantityToAssign: 0, value: 40, isRiddimBonus: true },
                    { name: 'Bonus 3', quantity: 200, quantityToAssign: 0, value: 30 },
                    { name: 'Bonus 4', quantity: 40, quantityToAssign: 0, value: 15, isRiddimBonus: true },
                    { name: 'Bonus 5', quantity: 120, quantityToAssign: 0, value: 25 },
                  ]}
                  isOptionEqualToValue={(option, value) => option.name === value.name}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        key={index}
                        label={`${option.name} ${option.isRiddimBonus ? '(Riddim)' : ''}`}
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  getOptionLabel={(option) => option.name}
                  renderOption={(props, option) => (
                    <Box component="li" sx={{ fontSize: 15, '& > span': { mr: '10px', fontSize: 18 } }} {...props}>
                      {`${option.name} Qty: ${option.quantity} ${option.isRiddimBonus ? '(Riddim)' : ''}`}
                    </Box>
                  )}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Bonuses"
                      error={Boolean(touched.bonuses && errors.bonuses)}
                      helperText={touched.bonuses && errors.bonuses}
                    />
                  )}
                />
                <TextField
                  select
                  fullWidth
                  size="small"
                  label="Select Ticket Rule"
                  {...getFieldProps('ticketRule')}
                  error={Boolean(touched.ticketRule && errors.ticketRule)}
                  helperText={touched.ticketRule && errors.ticketRule}
                >
                  {[
                    'Default Historical Data',
                    'Default App Level',
                    'Default Followers',
                    'Default Social',
                    'Historical V2',
                  ].map((option, index) => (
                    <MenuItem key={option + index} value={option}>
                      {option}
                    </MenuItem>
                  ))}
                </TextField>
                <FormControlLabel
                  labelPlacement="start"
                  control={<Switch {...getFieldProps('isDefault')} checked={values.isDefault} />}
                  label={<Typography variant="subtitle2">Default</Typography>}
                  sx={{ mx: 0, width: 1, justifyContent: 'end' }}
                />
              </Stack>
              {values.bonuses.length > 0 && (
                <FieldArray
                  name="bonuses"
                  render={() => (
                    <Card>
                      <CardHeader
                        title="Set Bonus Quantities for this set"
                        subheader={`Total value of this bonus set: ${values.bonuses.reduce(
                          (acc, bonus) => acc + bonus.quantityToAssign * bonus.value,
                          0
                        )}`}
                      />
                      <CardContent>
                        <Stack spacing={3}>
                          {values.bonuses.map((bonus, index) => {
                            const fieldName = `bonuses.${index}`;
                            return (
                              <TextField
                                key={index}
                                type="number"
                                fullWidth
                                size="small"
                                label={`${bonus.name} quantity (available ${bonus.quantity})`}
                                {...getFieldProps(`${fieldName}.quantityToAssign`)}
                                error={Boolean(
                                  getIn(touched, `${fieldName}.quantityToAssign`) &&
                                    getIn(errors, `${fieldName}.quantityToAssign`)
                                )}
                                helperText={
                                  (getIn(touched, `${fieldName}.quantityToAssign`) &&
                                    getIn(errors, `${fieldName}.quantityToAssign`)) ||
                                  `Bonus Value: ${bonus.value} Total Value: ${bonus.value * bonus.quantityToAssign}`
                                }
                              />
                            );
                          })}
                        </Stack>
                      </CardContent>
                    </Card>
                  )}
                />
              )}

              <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                  Save
                </LoadingButton>
              </Box>
            </Stack>
          </CardContent>
        </Card>
      </Form>
    </FormikProvider>
  );
}
