import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import BonusSetNewForm from '../components/bonus/BonusSetNewForm';

// ----------------------------------------------------------------------

export default function BonusSetCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  // TODO: will be updated with bonus
  const currentBonusSet = isEdit
    ? {
        name: 'Set 1',
        brand: 'Nike',
        bonuses: ['Bonus 1', 'Bonus 2', 'Bonus 3'],
        startDate: new Date(),
        duration: 20,
        ticketRules: ['Default Historical Data', 'Default Followers'],
      }
    : {};

  return (
    <Page title="BonusSet: Create a new bonus">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Add Bonus Set' : 'Edit Bonus Set'}
          links={[{ name: 'Bonus Set', href: PATH_DASHBOARD.root }, { name: !isEdit ? 'Add BonusSet' : name }]}
        />
        <BonusSetNewForm isEdit={isEdit} currentBonusSet={currentBonusSet} />
      </Container>
    </Page>
  );
}
